import component from './component'

let Vue = {}

let UltraComponent = {
  install (_Vue) {
    Vue = _Vue
    Vue.component('ultra-component', component)
  }
}

if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.component('ultra-component', UltraComponent)
}
export default UltraComponent



